@import url("https://necolas.github.io/normalize.css/8.0.1/normalize.css");
@font-face {
  font-family: 'Merriweather';
  font-style: normal;
  font-weight: 400;
  src: local('Merriweather Regular'), local('Merriweather-Regular'), url(https://fonts.gstatic.com/s/merriweather/v22/u-440qyriQwlOrhSvowK_l5-fCZJ.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Open Sans Regular'), local('OpenSans-Regular'), url(https://fonts.gstatic.com/s/opensans/v17/mem8YaGs126MiZpBA-UFVZ0e.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto Mono';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/robotomono/v12/L0xuDF4xlVMF-BfR8bXMIhJHg45mwgGEFl0_3vq_ROW9.ttf) format('truetype');
}
body {
  background-color: #444444;
  font-family: "Open Sans", sans-serif;
  white-space: pre-wrap;
}
body .App {
  width: 80vw;
  margin: auto;
  padding: 15px;
  background-color: #f1f1f1;
  box-shadow: 0 0 5px 5px #2e2e2e;
}
body .App .header {
  font-size: 1.5em;
  font-family: "Merriweather", serif;
}
body .App .row {
  padding: 1em;
  margin: 0.5em;
  border: 0.7px solid #dddddd;
  border-radius: 0.5em;
}
body .App .row:hover {
  background-color: #e9e9e9;
}
body .App .row .title {
  font-size: 1.2em;
  font-family: "Merriweather", serif;
}
body .App .row .subtitle {
  font-size: 0.95em;
  font-style: oblique;
  color: #303030;
}
body .App .row .description {
  margin: 0.5em 0 0.5em 0;
}
body .App .row .flex {
  font-size: 0.95em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-left: 2em;
}
body .App .row .flex .period-container {
  color: white;
  background-color: #303030;
  border: 3px solid #303030;
  border-radius: 5px;
  margin: 0.1em;
}
body .App .row .flex .period-container .period {
  font-size: 0.9em;
  font-family: "Roboto Mono", monospace;
}
body .App .profile-container .row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
body .App .profile-container .row .profile-image-container {
  padding-right: 1.5em;
}
body .App .profile-container .row .profile-image-container img {
  min-height: 115px;
  min-width: 115px;
  height: 115px;
  width: 115px;
  border-radius: 50%;
}
body .App .profile-container .row .profile-details-container {
  flex-grow: 2;
}
@media screen and (max-width: 1078px) {
  body .App .profile-container .row .profile-details-container {
    order: 2;
  }
}
body .App .profile-container .row .socialmedia-container {
  border: 0;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-self: flex-end;
}
body .App .profile-container .row .socialmedia-container img {
  padding: 1em;
  height: 3em;
  width: 3em;
}
body .App .profile-container .row .socialmedia-container img:hover {
  background-color: #f1f1f1;
}
body .App .skill-list {
  margin: 0.4em 0 0.4em 0;
  display: flex;
  flex-wrap: wrap;
}
body .App .skill-list .skill {
  margin: 0.1em;
  padding: 0 0.1em 0 0.1em;
  color: white;
  font-size: 0.9em;
  background-color: #474747;
  border: 3px solid #474747;
  border-radius: 5px;
}
body .App .hobbies-container .flex .hobbies-title {
  min-width: 8em;
}
body .App .hobbies-container .flex .hobbies-description {
  flex-grow: 1;
  margin: 0;
}
